let discount = [
  { value: '0', text: 'немає' }, 
  { value: '5', text: 'Діти: від 0 до 5р 5%' }, 
  { value: '10', text: 'Персіонер 10%' },
  { value: '15', text: 'Студент 15%' } ]
  
  
class FlavorForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: '0' };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  handleSubmit(event) {
    alert('Your favorite flavor is: ' + this.state.value);
    event.preventDefault();
  }

  render() {
    return (
      React.createElement("form", { onSubmit: this.handleSubmit },      

      React.createElement("select", { value: this.state.value, onChange: this.handleChange },
      discount.map(option => {
        return React.createElement("option", { value: option.value }, option.text);
      }) 
      
      ),

      React.createElement("pre", null, this.state.value)
      ));

  }}


ReactDOM.render(
React.createElement(FlavorForm, null),
document.getElementById('root'));